@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --sidebar-flex: 1.5;
  --condiv-flex: 6;
  --fontsize-p: 1.2rem;
  --fontsize-nav: 1.6rem;
  --typing-font: 3rem;
}
* {
  margin: 0;
  padding: 0;
}
body {
  font-family: 'Raleway';
}
.App {
  display: -webkit-flex;
  display: flex;
  width: 100vw;
  min-height: 100vh;
}
/* .condiv class is for the right side content container*/
.condiv {
  padding: 100px;
  background-color: #f4f5f9;
  -webkit-flex: 6;
          flex: 6;
  -webkit-flex: var(--condiv-flex);
          flex: var(--condiv-flex);
}

.nav-item{margin:40px}.nav-item:active{font-weight:bolder}
.nav-bar{padding:60px 30px 0px 30px;-webkit-flex:var(--sidebar-flex);flex:var(--sidebar-flex);background:#f2f3f7}.nav-bar__list{font-size:var(--fontsize-nav);text-align:center;list-style-type:none;text-decoration:none;color:#fff !important}
